import React, { Component } from "react";
import "./index.css";
import tshirt from "./images/tshirt.jpg";

class Textdesign extends Component {
  constructor(props) {
    super(props);
    this.state = { val: "", font: "", color: "", size: "50px" };
  }
  /////////////Function for seting state/////////////////
  changeText = (ev, inputType) => {
    switch (inputType) {
      case "textbox":
        this.setState({ val: ev.target.value });
        break;
      case "font":
        this.setState({ font: ev.target.value });
        break;
      case "color":
        this.setState({ color: ev.target.value });
        break;
      case "size":
        this.setState({ size: ev.target.value });
        break;
    }
  };

  render() {
    return (
      ////////////Tshirt Image///////////////
      <div>
        <div class="container">
          <img src={tshirt}></img>
          {/* ///////////Tshitt text////////////// */}
          <div
            class="innerContainer"
            style={{
              fontFamily: this.state.font,
              color: this.state.color,
              fontSize: this.state.size,
            }}
          >
            {this.state.val}
          </div>
        </div>

        <table>
          <tr>
            <td>
              <label>Enter Tshirt Text</label>
            </td>
            <td>
              <textarea
                id="tShirtText"
                cols="30"
                rows="7"
                onKeyUp={(ev) => this.changeText(ev, "textbox")}
              ></textarea>
            </td>
            <td>Font</td>
            <td>
              <select id="font" onChange={(ev) => this.changeText(ev, "font")}>
                <option value="null">--select--</option>
                <option value='"Comic Sans MS", cursive, sans-serif'>
                  "Comic Sans MS", cursive, sans-serif
                </option>
                <option value='"Arial Black", Gadget, sans-serif'>
                  "Arial Black", Gadget, sans-serif
                </option>
                <option value="Impact, Charcoal, sans-serif">
                  Impact, Charcoal, sans-serif
                </option>
              </select>
            </td>
            <td>Color</td>
            <td>
              <select
                id="color"
                onChange={(ev) => this.changeText(ev, "color")}
              >
                <option value="">--select--</option>
                <option value="red">Red</option>
                <option value="green">Green</option>
                <option value="blue">Blue</option>
              </select>
            </td>
            <td>Size</td>
            <td>
              <select id="size" onChange={(ev) => this.changeText(ev, "size")}>
                <option value="">--select--</option>
                <option value="5px">5</option>
                <option value="10px">10</option>
                <option value="15px">15</option>
                <option value="20px">20</option>
                <option value="25px">25</option>
                <option value="30px">30</option>
                <option value="35px">35</option>
                <option value="40px">40</option>
                <option value="45px">45</option>
                <option value="50px" selected>
                  50
                </option>
              </select>
            </td>
          </tr>
        </table>
      </div>
    );
  }
}

export default Textdesign;
